<template>
  <div id="main-object" class="sprite" :class="activeSprite"></div>
</template>

<script>
export default {
  async created() {
    while (!this.gameStarted) {
      await this.moving();
    }
  },
  props: {
    gameStarted: Boolean
  },
  data() {
    return {
      activeSprite: "sprite-sd0000"
    };
  },
  methods: {
    async moving() {
      for (let i = 0; i <= 132; i++) {
        await new Promise(resolve => {
          setTimeout(() => {
            const currentNumber = ("0000" + i).substring(i.toString().length);
            this.activeSprite = `sprite-sd${currentNumber}`;
            resolve();
          }, 50);
        });
      }

      return;
    }
  }
};
</script>


